body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgba(255, 255, 255, 1);
  background-image: radial-gradient(circle, rgba(255, 255, 255, 1) 0%, rgba(177, 200, 255, 1) 91%);
}

.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}